/**
 * txtareaSelectionStart: 
 * @param {*} dom domthis.$refs.dom
 * @param {*} method: 
 * @param {*} th:  - table
 * @param {*} td:  - table
 * @param {*} styles:  - table
 * @returns 
 */
export function txtareaSelectionStart(dom, method, th, td, styles){
    let vas
    var txtarea = dom;
    //textarea
    var start = txtarea.selectionStart;
    //textarea
    var finish = txtarea.selectionEnd;
    // 
    var allText = txtarea.value;
    //textarea
    var sel = allText? allText.substring(start, finish) : '';


    /**
     * 
     * @param {*} bold 
     * @param {*} italic 
     * @param {*} H11 
     * @returns 
     */
    const bold = function(value) {
       return value ? `**${value}**` : '****'
    }
    const italic = function(value) {
        return value ? `*${value}*` : '**'
    }
    const H1 = function(value) {
        return value ? `# ${value}` : '# 1'
    }
    const H2 = function(value) {
        return value ? `## ${value}` : '## 2'
    }
    const H3 = function(value) {
        return value ? `### ${value}` : '### 3'
    }
    const H4 = function(value) {
        return value ? `##### ${value}` : '##### 4'
    }
    const H5 = function(value) {
        return value ? `###### ${value}` : '###### 5'
    }
    const H6 = function(value) {
        return value ? `####### ${value}` : '####### 6'
    }
    const table = function() {
        let theader = '|'
        let tbody = '|'
        let thBody = ''
        let tgj = '|'
        if(td) {
          for(let i=0;i<td;i++) {
            tbody = tbody + 'column' + i + '|'
            theader = theader + 'column' + i + '|'
            if(styles === 'mr'){
                tgj = tgj + '-|'
            }
            if(styles === 'left'){
                tgj = tgj + ':-----|'
            }
            if(styles === 'center'){
                tgj = tgj + ':----:|'
            }
            if(styles === 'right'){
                tgj = tgj + '----:|'
            }
          }
        }
        if(th) {
            for(let i=0;i<th;i++) {
                thBody = thBody + tbody + '\n'
            }
        }
        let reg = /^([1-9][0-9]*)$/
        if(!reg.test(td) || !reg.test(th)) {
            return '|column1|column2|column3|\n|-|-|-|\n|content1|content2|content3|\n'
        }
        if(!td || !th) {
            return '|column1|column2|column3|\n|-|-|-|\n|content1|content2|content3|\n'
        } else {
            return theader + '\n' + tgj + '\n' + thBody + '\n'  
        }      
    }
    const alignleft = function(value) {
        return value ? `::: hljs-left\n\n ${value}\n\n:::\n` : '::: hljs-left\n\n\n\n:::\n'
    }
    const aligncenter = function(value) {
        return value ? `::: hljs-center\n\n ${value}\n\n:::\n` : '::: hljs-center\n\n\n\n:::\n'
    }
    const alignright = function(value) {
        return value ? `::: hljs-right\n\n ${value}\n\n:::\n` : '::: hljs-right\n\n\n\n:::\n'
    }
    // 
    switch(method) {
        case 'bold':
            vas = bold(sel)
            break;
        case 'italic':
            vas = italic(sel)
            break;
        case 'H1' :
            vas = H1(sel)
            break;
        case 'H2' :
            vas = H2(sel)
            break;
        case 'H3' :
            vas = H3(sel)
            break;
        case 'H4' :
            vas = H4(sel)
            break;
        case 'H5' :
            vas = H5(sel)
            break;
        case 'H6' :
            vas = H6(sel)
            break;
        case 'table' :
            vas = table(sel)
            break;
        case 'alignleft' :
            vas = alignleft(sel)
            break;
        case 'aligncenter' :
            vas = aligncenter(sel)
            break;
        case 'alignright' :
            vas = alignright(sel)
            break;
    }
    //
    var newText=allText.substring(0, start)+vas+allText.substring(finish, allText.length);
    return newText;
}
